import React, { Component } from 'react';
import { Row, Col, Form, Input } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import './style.scss';

@Form.create({ name: 'FormSearchStudent' })
class FormSearchStudent extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    const { code } = getQueryParams();
    form.setFieldsValue({ code });
  };

  handleReset = () => {
    this.props.form.resetFields();
    updateQueryParams({});
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams({ ...this.props.paramSearch, ...values });
    });
  };

  render() {
    const {
      form: { getFieldDecorator },
    } = this.props;
    return (
      <Form
        className="form-inline-label search-form"
        layout="vertical"
        onSubmit={this.handleSearch}
      >
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="M SV/SBD">{getFieldDecorator('code', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStudent;



