import { numberToMoney, dateToString } from 'utils/formatUtil';
import { Checkbox } from 'antd';
import React from 'react';

export const payableColumns = [
  {
    title: 'STT',
    dataIndex: 'index',
    key: 'index',
    align: 'center',
  },
  {
    title: 'Hc k',
    dataIndex: 'semester',
    key: 'semester',
    align: 'center',
  },
  {
    title: 'Nm hc',
    dataIndex: 'yearEdu',
    key: 'yearEdu',
    align: 'center',
  },
  {
    title: 'Khon thu',
    dataIndex: 'receivable',
    key: 'receivable',
  },
  {
    title: 'S tin phi np',
    dataIndex: 'amount',
    key: 'amount',
    align: 'right',
    render: text => numberToMoney(text),
  },
];

export const receiptColumns = [
  {
    title: 'STT',
    dataIndex: 'index',
    key: 'index',
    align: 'center',
  },
  {
    title: 'Hc k',
    dataIndex: 'semester',
    key: 'semester',
    align: 'center',
  },
  {
    title: 'Nm hc',
    dataIndex: 'yearEdu',
    key: 'yearEdu',
    align: 'center',
  },
  {
    title: 'K hiu',
    dataIndex: 'invoiceCode',
    key: 'invoiceCode',
    align: 'center',
  },
  {
    title: 'S phiu',
    dataIndex: 'invoiceNo',
    key: 'invoiceNo',
    align: 'center',
  },
  {
    title: 'Ngy thng',
    dataIndex: 'paymentDate',
    key: 'paymentDate',
    align: 'center',
    render: text => dateToString(text),
  },
  {
    title: 'Ngi thu/chi',
    dataIndex: 'casher',
    key: 'casher',
    align: 'center',
  },
  {
    title: 'Khon thu/chi',
    dataIndex: 'paymentContent',
    key: 'paymentContent',
    align: 'left',
  },
  {
    title: 'S tin',
    dataIndex: 'amount',
    key: 'amount',
    align: 'right',
    render: text => numberToMoney(text),
  },
  {
    title: 'Thu',
    dataIndex: 'isPayment',
    key: 'isPayment',
    align: 'center',
    render: text => <Checkbox checked={text} />,
  },
  {
    title: 'Chi',
    dataIndex: 'isOutcome',
    key: 'isOutcomes',
    align: 'center',
    render: text => <Checkbox checked={text} />,
  },
];

export const generalColumns = [
  {
    title: 'STT',
    dataIndex: 'index',
    key: 'index',
    align: 'center',
    width: '10%',
  },
  {
    title: 'Khon thu',
    dataIndex: 'receivable',
    key: 'receivable',
    align: 'left',
    width: '30%',
  },
  {
    title: 'S tin phi np',
    dataIndex: 'amount',
    key: 'amount',
    align: 'right',
    width: '15%',
    render: text => numberToMoney(text),
  },
  {
    title: 'S tin  np',
    dataIndex: 'paidAmt',
    key: 'paidAmt',
    align: 'right',
    width: '15%',
    render: text => numberToMoney(text),
  },
  {
    title: 'S tin hon tr',
    dataIndex: 'returnAmt',
    key: 'returnAmt',
    align: 'right',
    width: '15%',
    render: text => numberToMoney(text),
  },
  {
    title: 'S tin thiu/tha ',
    dataIndex: 'remainAmt',
    key: 'remainAmt',
    align: 'right',
    width: '15%',
    render: text => numberToMoney(text),
  },
];



