import { request } from 'utils';

const prefix = '/api/finance';
const studentPrefix = '/api/students';

export const getPayableAccounts = ({ code, page }) => {
  const defaultValue = {
    pageSize: 10,
    isPagingEnabled: true,
  };
  const paramSearch = {
    code,
    page,
    ...defaultValue,
  };
  return request({
    prefix,
    url: '/Receipt/GetReceivableByStudent',
    method: 'GET',
    params: { ...paramSearch },
    data: {},
  });
};

export const getReceipts = ({ code, page }) => {
  const defaultValue = {
    pageSize: 10,
    isPagingEnabled: true,
  };
  const paramSearch = {
    code,
    page,
    ...defaultValue,
  };
  return request({
    prefix,
    url: '/Receipt/GetReceiptByStudent',
    method: 'GET',
    params: { ...paramSearch },
    data: {},
  });
};

export const getGeneral = ({ code, page }) => {
  const defaultValue = {
    pageSize: 10,
    isPagingEnabled: true,
  };
  const paramSearch = {
    code,
    page,
    ...defaultValue,
  };
  return request({
    prefix,
    url: '/Receipt/GetGeneralFinanceByStudent',
    method: 'GET',
    params: { ...paramSearch },
    data: {},
  });
};

export const getStudentByCode = async code =>
  request({
    prefix: studentPrefix,
    url: `/Student/GetByCode/${code}`,
    method: 'GET',
    params: {},
    data: {},
  });



