import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnExport } from 'components/common/button';
import { Card, Row, Col, message } from 'antd';
import { CartHeadAction, TableData } from 'components/common';
import {
  getPayableAccounts,
  getReceipts,
  getGeneral,
  getStudentByCode,
} from 'services/tuitionFeeStudent';
import { getQueryParams } from 'utils/urlUtil';
import { numberToMoney } from 'utils/formatUtil';
import FormSearchStudent from './FormSearchStudent';
import { StudentProfile } from './StudentProfile';
import { payableColumns, receiptColumns, generalColumns } from './columnConfig';
import './style.scss';

const initialValues = {
  reload: 0,
  paramSearch: {},
  addingStudents: {},
  addingStudentsTableData: {},
  deletingStudents: {},
  totalAmount: 0,
  totalPaidAmt: 0,
  totalRemainAmt: 0,
  totalReturnAmt: 0,
  student: {
    fullname: '',
    className: '',
  },
};
class TuitionAndFeesStudentPage extends Component {
  state = {
    ...initialValues,
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  onSelectForAddStudent = (selectedStudent, isSelect) => {
    const addingStudents = { ...this.state.addingStudents };
    if (isSelect) {
      addingStudents[selectedStudent.studentId] = selectedStudent;
    } else {
      delete addingStudents[selectedStudent.studentId];
    }
    this.setState({ addingStudents });
  };

  onSelectAllForAddStudent = (selectedStudents, isSelect) => {
    if (isSelect) {
      const addingStudents = {};
      selectedStudents.forEach(student => {
        addingStudents[student.studentId] = student;
      });
      this.setState({ addingStudents });
    } else {
      this.setState({ addingStudents: {} });
    }
  };

  onSelectForRemoveStudent = (selectedStudent, isSelect) => {
    const deletingStudents = { ...this.state.deletingStudents };
    if (isSelect) {
      deletingStudents[selectedStudent.studentId] = selectedStudent;
    } else {
      delete deletingStudents[selectedStudent.studentId];
    }
    this.setState({
      deletingStudents,
    });
  };

  onSelectAllForRemoveStudent = (selectedStudents, isSelect) => {
    if (isSelect) {
      const deletingStudents = {};
      selectedStudents.forEach(student => {
        deletingStudents[student.studentId] = student;
      });
      this.setState({ deletingStudents });
    } else {
      this.setState({ deletingStudents: {} });
    }
  };

  onAddStudents = () => {
    const addingStudentsTableData = { ...this.state.addingStudentsTableData };
    Object.entries(this.state.addingStudents).forEach(([key, value]) => {
      addingStudentsTableData[key] = value;
    });
    this.setState({ addingStudentsTableData });
  };

  getStudentInfo = async paramSearch => {
    const { code } = paramSearch;
    if (!code) return;
    try {
      const res = await getStudentByCode(code);
      if (!res || !res.data) return;
      const student = {};
      student.fullname = `${res.data.firstname} ${res.data.lastname}`;
      student.className = res.data.className;
      this.setState({ student });
    } catch (err) {
      // case cancel when rerender
      if (!err.message) return;
      this.setState({ ...initialValues });
      message.error(`Khng tm thy sinh vin vi m ${code}`);
    }
  };

  getPayableAccounts = async paramSearch => {
    const res = await getPayableAccounts(paramSearch);
    this.addIndex(res);
    return res;
  };

  getReceipts = async paramSearch => {
    const res = await getReceipts(paramSearch);
    this.addIndex(res);
    return res;
  };

  getGeneralCustom = async paramSearch => {
    const res = await getGeneral(paramSearch);
    this.addIndex(res);
    this.updateTotalTitle(res);
    return res;
  };

  updateTotalTitle = res => {
    let totalAmount = 0;
    let totalPaidAmt = 0;
    let totalRemainAmt = 0;
    let totalReturnAmt = 0;
    res.data.items.forEach(item => {
      totalAmount += item.amount;
      totalPaidAmt += item.paidAmt;
      totalRemainAmt += item.remainAmt;
      totalReturnAmt += item.returnAmt;
    });
    this.setState({
      totalAmount,
      totalPaidAmt,
      totalRemainAmt,
      totalReturnAmt,
    });
  };

  addIndex = res => {
    let index = 0;
    res.data.items.forEach(item => {
      index += 1;
      item.index = index;
    });
  };

  handleChangeQueryParams = async () => {
    const paramSearch = getQueryParams();
    await this.getStudentInfo(paramSearch);
    this.setState({
      paramSearch,
    });
  };

  render() {
    const {
      reload,
      paramSearch,
      addingStudents,
      student,
      totalAmount,
      totalPaidAmt,
      totalRemainAmt,
      totalReturnAmt,
    } = this.state;

    const { code } = paramSearch;

    const isBtnExportAndPrintDisable = addingStudents
      ? Object.entries(addingStudents).length < 1
      : false;

    return (
      <Card title="DS lp hc">
        <CartHeadAction>
          <div className="btn-group">
            <BtnExport
              className="btnExport"
              title="Xut Excel"
              disabled={isBtnExportAndPrintDisable}
            />
          </div>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearchStudent />
          </Col>
        </Row>
        <Row className="student-profile">
          <Col>
            <StudentProfile student={student} />
          </Col>
        </Row>
        <Row className="payable-accounts">
          <Col span={24}>
            <TableData
              reload={reload}
              service={this.getPayableAccounts}
              paramSearch={paramSearch}
              allowGetData={!!code}
              columns={payableColumns}
              rowKey="index"
              className="payable-accounts-table"
              title={() => (
                <div>
                  <strong>I. CC KHON PHI NP</strong>
                </div>
              )}
            />
          </Col>
        </Row>
        <Row className="payable-accounts">
          <Col span={24}>
            <TableData
              reload={reload}
              service={this.getReceipts}
              paramSearch={paramSearch}
              allowGetData={!!code}
              columns={receiptColumns}
              rowKey="index"
              title={() => (
                <div>
                  <strong>II. CC BIN LAI THU/CHI</strong>
                </div>
              )}
            />
          </Col>
        </Row>
        <Row className="overal-table">
          <Col span={24}>
            <TableData
              reload={reload}
              service={this.getGeneralCustom}
              paramSearch={paramSearch}
              allowGetData={!!code}
              columns={generalColumns}
              rowKey="index"
              title={() => (
                <Row className="overal-table-header">
                  <div className="title">
                    <strong>III. TNG HP CHUNG</strong>
                  </div>
                  <div className="total-dept-balance">{numberToMoney(totalAmount)}</div>
                  <div className="total-deposite-money">{numberToMoney(totalPaidAmt)}</div>
                  <div className="total-return-money">{numberToMoney(totalReturnAmt)}</div>
                  <div className="total-submit-money">{numberToMoney(totalRemainAmt)}</div>
                </Row>
              )}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TuitionAndFeesStudentPage />
  </Page>
);



