import React, { Fragment } from 'react';
import './style.scss';

export const StudentProfile = ({ student }) => (
  <Fragment>
    <div className="student-profile-name">
      <div className="student-profile-name-title">H tn:</div>
      <div>
        <strong style={{ paddingLeft: '10px' }}>{student.fullname}</strong>
      </div>
    </div>
    <div className="student-profile-class">
      <div className="student-profile-class-title">Lp:</div>
      <div>
        <strong style={{ paddingLeft: '10px' }}>{student.className}</strong>
      </div>
    </div>
  </Fragment>
);



